<template>
  <v-dialog width="400px" persistent v-model="editDialog">
    <v-btn fab accent slot="activator">
      <v-icon>edit</v-icon>
    </v-btn>
        <v-card>
          <v-container>
            <v-layout row wrap>
              <v-flex xs12>
                <v-card-title>Edit Checkup</v-card-title>
              </v-flex>
            </v-layout>
            <v-divider></v-divider>
            <v-layout row wrap>
              <v-flex xs12>
                <v-card-text>
                  <v-text-field
                    name="stockName"
                    label="Stock Name"
                    id="stockName"
                    v-model="editedstockName"
                    required></v-text-field>
                    <v-text-field
                      name="vetName"
                      label="Vet Name"
                      id="vetName"
                      v-model="editedvetName"
                      required></v-text-field>
                    <v-text-field
                      name="date"
                      label="Date"
                      id="date"
                      v-model="editedDate"
                      required></v-text-field>
                </v-card-text>
              </v-flex>
            </v-layout>
            <v-divider></v-divider>
            <v-layout row wrap>
              <v-flex xs12>
                <v-card-actions>
                  <v-btn
                  flat
                  class="blue--text darken-1"
                  @click="editDialog = false">Close</v-btn>
                    <v-btn flat class="blue--text darken-1" @click="onSaveChanges">Save</v-btn>
                </v-card-actions>
                  </v-flex>
              </v-layout>
          </v-container>
      </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['checkups'],
  data () {
    return {
      editDialog: false,
      editedstockName: this.checkups.stockName.text,
      editedvetName: this.checkups.vetName.text,
      editedDate: this.checkups.date
    }
  },
  methods: {
    onSaveChanges () {
      if (this.editedstockName.trim() === '' ||
    this.editedvetName.trim() === '' ||
    this.editedDate.trim() === '') {
        return
      }
      this.editDialog = false
      this.$store.dispatch('updateCheckupData', {
        id: this.checkups.id,
        stockName: this.editedstockName,
        vetName: this.editedvetName,
        date: this.editedDate
      })
    }
  }
}
</script>



