export const getters = {
  appTitle (state) {
    return state.appTitle
  },
  getUser (state) {
    return state.firebaseUser
  },
  loading (state) {
    return state.loading
  },
  getError (state) {
    return state.error
  },
  getLoading (state) {
    return state.loading
  },
  getUserType (state) {
    return state.loadedUsers.find((users) => {
      return users.userType
    })
  },
  getUserVet (state) {
    return state.loadedUsers.find((users) => {
      return users.name.text
    })
  },
  getUserName (state) {
    return state.loadedVetName.find((users) => {
      return users.name
    })
  },
  loadedLivestock (state) {
    return state.loadedLivestock
  },
  loadedUsers (state) {
    return state.loadedUsers.sort((usersA, usersB) => {
      return usersA.name > usersB.name
    })
  },
  loadedCheckups (state) {
    return state.loadedCheckups.sort((checkupA, checkupB) => {
      return checkupA.date > checkupB.date
    })
  },
  loadOneLivestock (state) {
    return (livestockId) => {
      return state.loadedLivestock.find((livestock) => {
        return livestock.id === livestockId
      })
    }
  }
}



