<template>
  <v-dialog width="400px" persistent v-model="editDialog">
    <v-btn fab accent slot="activator">
    <v-icon>edit</v-icon>
  </v-btn>
  <v-card>
    <v-container>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card-title>Edit Livestock</v-card-title>
      </v-flex>
    </v-layout>
    <v-divider></v-divider>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card-text>
          <v-text-field
            name="stockName"
            label="Stock Name"
            id="stockName"
            v-model="editedstockName"
            required></v-text-field>
            <v-text-field
              name="breed"
              label="Breed"
              id="breed"
              v-model="editedBreed"
              required></v-text-field>
              <v-text-field
                name="numInStock"
                label="Number in Stock"
                id="numInStock"
                v-model="editednumInStock"
                required></v-text-field>
                <v-text-field
                  name="addInfo"
                  label="Additional Information"
                  id="addInfo"
                  multi-line
                  v-model="editedaddInfo"
                  required></v-text-field>
        </v-card-text>
      </v-flex>
    </v-layout>
    <v-divider></v-divider>
    <v-layout row wrap>
      <v-flex xs12>
<v-card-actions>
  <v-btn
  flat
  class="blue--text darken-1"
  @click="editDialog = false">Close</v-btn>
    <v-btn flat class="blue--text darken-1" @click="onSaveChanges">Save</v-btn>
</v-card-actions>
      </v-flex>
    </v-layout>
  </v-container>
  </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['livestock'],
  data () {
    return {
      editDialog: false,
      editedstockName: this.livestock.stockName,
      editedBreed: this.livestock.breed,
      editednumInStock: this.livestock.numInStock,
      editedaddInfo: this.livestock.addInfo
    }
  },
  methods: {
    onSaveChanges () {
      if (this.editedstockName.trim() === '' ||
      this.editedBreed.trim() === '' ||
     this.editednumInStock.trim() === '' ||
     this.editedaddInfo.trim() === '') {
        return
      }
      this.editDialog = false
      this.$store.dispatch('updateLivestockData', {
        id: this.livestock.id,
        stockName: this.editedstockName,
        breed: this.editedBreed,
        numInStock: this.editednumInStock,
        addInfo: this.editedaddInfo,
        date: this.livestock.date
      })
    }
  }
}
</script>



