import Vue from 'vue'
import Vuetify from 'vuetify'
import App from './App'
import router from './router'
import { store } from './store'
import { firebaseConfig } from './config'
import EditLivestockDetailsDialogue from './components/DialogPopUps/EditLivestockDetailsDialogue.vue'
import EditCheckupDialog from './components/DialogPopUps/EditCheckupDialog.vue'
import RegisterBirthDialog from './components/DialogPopUps/RegisterBirthDialog.vue'
import 'vue-event-calendar/dist/style.css'
import vueEventCalendar from 'vue-event-calendar'
import 'firebase/firestore'
import * as firebase from 'firebase'

Vue.use(vueEventCalendar, {locale: 'en'})
Vue.use(Vuetify)
Vue.config.productionTip = false
Vue.component('app-edit-livestock-details-dialog', EditLivestockDetailsDialogue)
Vue.component('app-edit-checkup-dialog', EditCheckupDialog)
Vue.component('app-register-birth-dialog', RegisterBirthDialog)
firebase.initializeApp(firebaseConfig)
const unsubscribe = firebase.auth()
.onAuthStateChanged((firebaseUser) => {
  /* eslint-disable no-new */
  new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App),
    created () {
      store.dispatch('autoSignIn', firebaseUser)
      this.$store.dispatch('loadLivestock')
      this.$store.dispatch('loadCheckup')
    }
  })
  unsubscribe()
})



