export const mutations = {
  setUser (state, payload) {
    state.firebaseUser = payload
  },
  setError (state, payload) {
    state.error = payload
  },
  setLoading (state, payload) {
    state.loading = payload
  },
  updateLivestock (state, payload) {
    const livestock = state.loadedLivestock.find(livestock => {
      return livestock.id === payload.id
    })
    if (payload.stockName) {
      livestock.stockName = payload.stockName
    }
    if (payload.breed) {
      livestock.breed = payload.breed
    }
    if (payload.numInStock) {
      livestock.numInStock = payload.numInStock
    }
    if (payload.addInfo) {
      livestock.addInfo = payload.addInfo
    }
    if (payload.date) {
      livestock.date = payload.date
    }
  },
  updateBirth (state, payload) {
    const livestock = state.loadedLivestock.find(livestock => {
      return livestock.id === payload.id
    })
    if (payload.numInStock) {
      livestock.numInStock = payload.numInStock
    }
  },
  updateCheckup (state, payload) {
    const checkups = state.loadedCheckups.find(checkups => {
      return checkups.id === payload.id
    })
    if (payload.stockName) {
      checkups.stockName.text = payload.stockName
    }
    if (payload.vetName) {
      checkups.vetName.text = payload.vetName
    }
    if (payload.date) {
      checkups.date = payload.date
    }
  },
  adduserType (state, payload) {
    state.loadedUsers.push(payload)
  },
  addUserName (state, payload) {
    state.loadedVetName.push(payload)
  },
  setLoadedLivestock (state, payload) {
    state.loadedLivestock = payload
  },
  addLivestock (state, payload) {
    state.loadedLivestock = payload
  },
  setLoadedCheckups (state, payload) {
    state.loadedCheckups = payload
  },
  addCheckup (state, payload) {
    state.loadedCheckups.push(payload)
  }
}



