<template>
  <v-dialog width="400px" persistent v-model="editDialog">
    <v-btn accent slot="activator">
    Register Birth or Death
  </v-btn>
  <v-card>
    <v-container>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card-title>Register Birth/Death</v-card-title>
      </v-flex>
    </v-layout>
    <v-divider></v-divider>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card-text>
          <v-text-field
            name="numInStock"
            label="Add to Stock"
            id="numInStock"
            v-model="editednumInStock"
            required></v-text-field>
        </v-card-text>
      </v-flex>
    </v-layout>
    <v-divider></v-divider>
    <v-layout row wrap>
      <v-flex xs12>
<v-card-actions>
  <v-btn
  flat
  class="blue--text darken-1"
  @click="editDialog = false">Close</v-btn>
    <v-btn flat class="blue--text darken-1" @click="onSaveChanges">Save</v-btn>
</v-card-actions>
      </v-flex>
    </v-layout>
  </v-container>
  </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['livestock'],
  data () {
    return {
      editDialog: false,
      editednumInStock: this.livestock.numInStock
    }
  },
  methods: {
    onSaveChanges () {
      if (this.editednumInStock.trim() === '') {
        return
      }
      this.editDialog = false
      this.$store.dispatch('updateBirthData', {
        id: this.livestock.id,
        numInStock: this.editednumInStock
      })
    }
  }
}
</script>



