import * as firebase from 'firebase'
import router from '@/router'
export const actions = {
  loadLivestock ({commit, getters}) {
    commit('setLoading', true)
    firebase.database().ref('livestock').once('value')
    .then((data) => {
      const livestockInfo = []
      const obj = data.val()
      for (let key in obj) {
        if (getters.getUser.id === obj[key].creatorId) {
          livestockInfo.push({
            id: key,
            livestockId: obj[key].livestockId,
            stockName: obj[key].stockName,
            breed: obj[key].breed,
            numInStock: obj[key].numInStock,
            date: obj[key].date,
            addInfo: obj[key].addInfo,
            creatorId: obj[key].creatorId
          })
        }
      }
      commit('setLoadedLivestock', livestockInfo)
      commit('setLoading', false)
    })
    .catch(
      (error) => {
        console.log(error)
        commit('setLoading', false)
      })
  },
  loadCheckup ({commit, getters}) {
    commit('setLoading', true)
    firebase.database().ref('checkups').once('value')
    .then((data) => {
      const checkupInfo = []
      const obj = data.val()
      for (let key in obj) {
        if (getters.getUser.id === obj[key].creatorId) {
          checkupInfo.push({
            id: key,
            stockName: obj[key].stockName,
            date: obj[key].date,
            vetName: obj[key].vetName,
            creatorId: obj[key].creatorId
          })
        } else if (getters.getUserName.name === obj[key].vetName) {
          checkupInfo.push({
            id: key,
            stockName: obj[key].stockName,
            date: obj[key].date,
            vetName: obj[key].vetName,
            creatorId: obj[key].creatorId
          })
        }
      }
      commit('setLoadedCheckups', checkupInfo)
      commit('setLoading', false)
    })
    .catch(
      (error) => {
        console.log(error)
        commit('setLoading', false)
      })
  },
  updateLivestockData ({commit}, payload) {
    const updateObj = {}
    if (payload.stockName) {
      updateObj.stockName = payload.stockName
    }
    if (payload.breed) {
      updateObj.breed = payload.breed
    }
    if (payload.numInStock) {
      updateObj.numInStock = payload.numInStock
    }
    if (payload.addInfo) {
      updateObj.addInfo = payload.addInfo
    }
    if (payload.date) {
      updateObj.date = payload.date
    }
    firebase.database().ref('livestock').child(payload.id).update(updateObj)
    .then(() => {
      commit('setLoading', false)
      commit('updateLivestock', payload)
    })
    .catch(error => {
      console.log(error)
      commit('setLoading', false)
    })
  },
  updateBirthData ({commit}, payload) {
    const updateObj = {}
    if (payload.numInStock) {
      updateObj.numInStock = payload.numInStock
    }

    firebase.database().ref('livestock').child(payload.id).update(updateObj)
    .then(() => {
      commit('setLoading', false)
      commit('updateBirth', payload)
    })
    .catch(error => {
      console.log(error)
      commit('setLoading', false)
    })
  },
  updateCheckupData ({commit}, payload) {
    const updateObj = {}
    if (payload.stockName) {
      updateObj.stockName = payload.stockName
    }
    if (payload.vetName) {
      updateObj.vetName = payload.vetName
    }
    if (payload.date) {
      updateObj.date = payload.date
    }
    firebase.database().ref('checkups').child(payload.id).update(updateObj)
    .then(() => {
      commit('setLoading', false)
      commit('updateCheckup', payload)
    })
    .catch(error => {
      console.log(error)
      commit('setLoading', false)
    })
  },
  userSignUp ({commit, getters}, payload) {
    commit('setLoading', true)
    firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
    .then(firebaseUser => {
      commit('setUser', firebaseUser)
      commit('setLoading', false)
      router.push('/usertype')
    })
    .catch(error => {
      commit('setError', error.message)
      commit('setLoading', false)
    })
  },
  userSignIn ({commit, getters}, payload) {
    commit('setLoading', true)
    firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
    .then(firebaseUser => {
      commit('setUser', firebaseUser)
      commit('setLoading', false)
      commit('setError', null)
    })
    .catch(error => {
      commit('setError', error.message)
      commit('setLoading', false)
    })
  },
  autoSignIn ({commit, getters}, payload) {
    commit('setUser', {id: payload.uid, livestock: []})
  },
  userSignOut ({commit}) {
    firebase.auth().signOut()
    commit('setUser', null)
    router.push('/')
  },
  addLivestock ({commit, getters}, payload) {
    console.log(payload)
    const livestock = {
      livestockId: payload.livestockId,
      stockName: payload.stockName,
      breed: payload.breed,
      numInStock: payload.numInStock,
      date: payload.date,
      addInfo: payload.addInfo,
      creatorId: getters.getUser.id
    }
    firebase.database().ref('livestock').push(livestock)
    .then((data) => {
      const key = data.key
      commit('addLivestock', {
        ...livestock,
        id: key
      })
    })
    .catch((error) => {
      console.log(error)
    })
  },
  adduserType ({commit, getters}, payload) {
    const userTypeData = {
      name: payload.name,
      uid: getters.getUser.uid || null,
      userType: payload.userType
    }
    firebase.database().ref('users').push(userTypeData)
    .then((data) => {
      const key = data.key
      commit('adduserType', {
        ...userTypeData,
        id: key
      })
    })
    .catch((error) => {
      console.log(error)
    })
  },
  addCheckup ({commit, getters}, payload) {
    const checkup = {
      stockName: payload.stockName,
      date: payload.date.toISOString(),
      vetName: payload.vetName,
      creatorId: getters.getUser.id
    }
    firebase.database().ref('checkups').push(checkup)
    .then((data) => {
      const key = data.key
      commit('addCheckup', {
        ...checkup,
        id: key
      })
    })
    .catch((error) => {
      console.log(error)
    })
  }
}



